@import url("https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed|Roboto+Slab|Sura|Open+Sans&display=swap");
body:before {
  content: '';
  display: block;
  background-color: #f79678;
  position: absolute;
  height: 0.45rem;
  width: 100vw;
}
body #canvas-wrapper,
body #content-wrapper {
  display: flex;
  flex-direction: column;
}
body #content-wrapper {
  flex-grow: 1;
}
body #canvas-wrapper {
  min-height: 100vh;
}
body #canvas-wrapper header,
body #canvas-wrapper main,
body #canvas-wrapper footer {
  padding: 1rem;
}
.canvas-wrapper * {
  color: #4d4d4d;
}
body #canvas-wrapper {
  font-family: 'Roboto', 'Frutiger', 'Frutiger Linotype', Univers, Calibri, 'Gill Sans', 'Gill Sans MT', 'Myriad Pro', Myriad, 'DejaVu Sans Condensed', 'Liberation Sans', 'Nimbus Sans L', Tahoma, Geneva, 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
body #canvas-wrapper * h1,
body #canvas-wrapper * h2,
body #canvas-wrapper * h3,
body #canvas-wrapper * h4,
body #canvas-wrapper * h5,
body #canvas-wrapper * h6,
body #canvas-wrapper * h7 {
  font-family: 'Roboto Slab', 'Palatino Linotype', Palatino, Palladio, 'URW Palladio L', 'Book Antiqua', Baskerville, 'Bookman Old Style', 'Bitstream Charter', 'Nimbus Roman No9 L', Garamond, 'Apple Garamond', 'ITC Garamond Narrow', 'New Century Schoolbook', 'Century Schoolbook', 'Century Schoolbook L', Georgia, serif;
  margin-top: 0;
  line-height: 1;
  color: #000;
}
.canvas-wrapper header a.site-logo img {
  height: 3rem;
  margin-top: -3.2px;
  width: auto;
}
.canvas-wrapper header a.site-name {
  text-decoration: none;
  font-size: 1.3rem;
  text-transform: uppercase;
  font-family: 'Roboto Slab', 'Palatino Linotype', Palatino, Palladio, 'URW Palladio L', 'Book Antiqua', Baskerville, 'Bookman Old Style', 'Bitstream Charter', 'Nimbus Roman No9 L', Garamond, 'Apple Garamond', 'ITC Garamond Narrow', 'New Century Schoolbook', 'Century Schoolbook', 'Century Schoolbook L', Georgia, serif;
  color: #000;
}
.canvas-wrapper header p.site-slogan {
  margin: 0;
}
body.toolbar-vertical #canvas-wrapper .content-wrapper {
  margin-top: 46px;
  padding-top: 81px;
}
footer {
  margin-top: auto;
}
.region-header {
  grid-column: 1/3;
}
body.toolbar-vertical #canvas-wrapper {
  margin-top: -39px;
}
body.toolbar-vertical #canvas-wrapper header {
  margin-top: 46px;
  display: grid;
  position: fixed;
  background-color: #fff;
  left: 0;
  right: 0;
}
body.path-frontpage header {
  z-index: 3;
}
body.path-frontpage header #block-wellspring-branding.block-branding {
  align-items: center;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 1rem;
}
body.path-frontpage header #block-wellspring-branding.block-branding h1 {
  margin: 0;
}
.menu-button {
  display: none;
}
.menu-button:checked ~ .region-navigation {
  max-height: 340px;
  width: 100%;
  height: 100%;
}
.menu-button:checked ~ .menu-icon .nav-icon {
  background: transparent;
}
.menu-button:checked ~ .menu-icon .nav-icon:before {
  transform: rotate(-45deg);
  top: 0;
  width: 100%;
}
.menu-button:checked ~ .menu-icon .nav-icon:after {
  transform: rotate(45deg);
  top: 0;
}
.menu-icon {
  position: relative;
  margin: auto;
  cursor: pointer;
  grid-column: 3/4;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-icon .nav-icon {
  background-color: #000;
  border-radius: 1px;
  display: block;
  height: 4px;
  width: 24px;
  position: relative;
  transition: background 0.2s ease-out;
}
.menu-icon .nav-icon:before {
  content: '';
  background-color: #000;
  border-radius: 1px;
  display: block;
  height: 100%;
  width: 65%;
  position: absolute;
  transition: all 0.2s ease-out;
  top: 7px;
}
.menu-icon .nav-icon:after {
  content: '';
  background-color: #000;
  border-radius: 1px;
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  top: -7px;
}
@media only screen and (min-width: 800px) {
  .menu-icon {
    display: none;
  }
}
header .region-navigation {
  grid-column: 1/4;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  clear: both;
  height: 0;
  max-height: 0;
  transition: max-height 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}
header .region-navigation nav ul {
  padding: 0;
  margin: 0;
}
header .region-navigation nav ul li.nav-item {
  padding: 0;
  position: relative;
  border-bottom: 1px solid #fff;
}
header .region-navigation nav ul li.nav-item span {
  padding: 12px;
}
header .region-navigation nav ul li.nav-item.is-active-trail a {
  color: primary;
}
header .region-navigation nav ul li.nav-item a {
  font-family: 'Roboto Slab', 'Palatino Linotype', Palatino, Palladio, 'URW Palladio L', 'Book Antiqua', Baskerville, 'Bookman Old Style', 'Bitstream Charter', 'Nimbus Roman No9 L', Garamond, 'Apple Garamond', 'ITC Garamond Narrow', 'New Century Schoolbook', 'Century Schoolbook', 'Century Schoolbook L', Georgia, serif;
  text-decoration: none;
  padding: 0 15px 15px 16px;
  text-align: left;
  color: text;
  display: block;
}
header .region-navigation nav ul li.nav-item:first-child a {
  padding: 4px 15px 15px 16px;
}
body.maintenance-page #canvas-wrapper {
  display: grid;
  justify-content: center;
  align-content: center;
}
body.maintenance-page #canvas-wrapper header.header {
  display: grid;
  justify-content: center;
}
body.maintenance-page #canvas-wrapper header.header .brand-logo,
body.maintenance-page #canvas-wrapper header.header .brand-heading {
  text-align: center;
}
body.maintenance-page #canvas-wrapper header.header .brand-logo h1,
body.maintenance-page #canvas-wrapper header.header .brand-heading h1,
body.maintenance-page #canvas-wrapper header.header .brand-logo P,
body.maintenance-page #canvas-wrapper header.header .brand-heading P {
  margin: 0;
}
body.maintenance-page #canvas-wrapper header.header .brand-logo {
  height: 80px;
}
body.maintenance-page #canvas-wrapper .content-wrapper {
  text-align: center;
}
